type IEventHandler<E, T> = (event: E, type?: T) => boolean | void;

export default class EventEmitter<T extends string, E> {
  events: {
    [key in T]?: {
      pool: Array<IEventHandler<E, T>>;
      single: number;
    };
  } = {};
  constructor() {
    this.events = {};
  }
  one(type: T, handler: IEventHandler<E, T>, single?: boolean) {
    const onceHandler =
      typeof handler === 'function'
        ? (...args: [E, T?]) => {
            // è¯¥äºä»¶åªæ§è¡ä¸æ¬¡ï¼æ§è¡å®å°±è§£é¤äºä»¶
            handler.apply(null, args);
            this.off(type, onceHandler, single);
          }
        : handler;
    this.on(type, onceHandler, single);
  }
  on(type: T, handler: IEventHandler<E, T>, single?: boolean) {
    const events = this.events[type] || {
      pool: [],
      single: -1,
    };
    if (typeof handler === 'function') {
      if (single) {
        // è¯¥äºä»¶åªè½æ³¨åä¸æ¬¡ï¼æ¯æ¬¡æ³¨åé½ä¼æ¿æ¢ä¸æ¬¡æ³¨åçï¼ç±»ä¼¼äºdomå±æ§äºä»¶èµå¼æ³¨åæ¯å¦element.onclick = ()=>{}
        if (events.single === -1) {
          // è®°å½è¯¥åç¬äºä»¶å¨ææäºä»¶çä½ç½®
          events.single = events.pool.push(handler) - 1;
        } else {
          events.pool[events.single] = handler;
        }
      } else {
        // è¯¥äºä»¶å¯ä»¥æ³¨åå¤æ¬¡ï¼æ§è¡æ¶ï¼ä¼éåå¨é¨äºä»¶å¨é¨æ§è¡ï¼ç±»ä¼¼äºdomçaddEventListener
        // æ³¨åè¿å»ä¹åä¼æ£æ¥æ¯å¦æç¸åçå¤çç¨åºï¼å¦ææï¼åä¸åæ·»å ï¼ç¬ç«ç¨åºä¸åä¸ï¼
        let unregistered = true;
        for (let i = 0, len = events.pool.length; i < len; i++) {
          if (events.pool[i] === handler && i !== events.single) {
            unregistered = false;
            break;
          }
        }
        if (unregistered) {
          events.pool.push(handler);
        }
      }
    } else if (single && events.single !== -1) {
      // éè¦æç¬ç«äºä»¶å é¤ï¼ç¸å½äºè§£ç»ç¬ç«äºä»¶
      events.pool.splice(events.single, 1);
      events.single = -1;
    }
    this.events[type] = events;
  }
  off(type?: T, handler?: IEventHandler<E, T>, single?: boolean) {
    if (typeof type === 'undefined') {
      // æ²¡ætypeåå é¤å¨é¨äºä»¶
      this.events = {};
    } else if (typeof handler === 'undefined') {
      // å é¤typeä¸çææäºä»¶
      delete this.events[type];
    } else if (single) {
      const events = this.events[type];
      if (events && events.single !== -1) {
        // å é¤ç¬ç«ç¨åºäºä»¶
        events.pool.splice(events.single, 1);
        events.single = -1;
      }
    } else {
      const events = this.events[type];
      if (events) {
        // æ£æ¥å¹¶å é¤äºä»¶æ± åäºä»¶
        for (let i = events.pool.length - 1; i >= 0; i--) {
          if (events.pool[i] === handler && i !== events.single) {
            events.pool.splice(i, 1);
            // å ä¸ºç¸åäºä»¶åªä¼æä¸ä¸ªï¼æä»¥å é¤åä¸éè¦åæ£æ¥äº
            break;
          }
        }
      }
    }
  }
  emit(type: T, event: E) {
    const events = this.events[type];
    if (events) {
      // å¾ªç¯æ§è¡äºä»¶æ± éçäºä»¶
      for (let i = 0, len = events.pool.length; i < len; i++) {
        const handler = events.pool[i];
        if (typeof handler === 'function') {
          const immediatePropagation = handler.apply(null, [event, type]);
          // è¿åå¼ä¸ºfalseï¼åé»æ­¢åäºè¯¥äºä»¶æ³¨åçåç±»åäºä»¶è§¦å
          if (immediatePropagation === false) {
            break;
          }
        }
      }
    }
  }
}
