// å½æ°ç¼©æ¾ k åï¼å½æ°èªåétåä¸ºt/kï¼æ´ä½ç»æf(t)*kï¼
// åå¹³ç§»åç¼©æ¾ï¼s = (f((t/k)-x)+y)*k
// åç¼©æ¾åå¹³ç§»ï¼s = (f((t-x)/k)*k)+y

export type EaseFn = (t: number) => number;

const baseEase: { [key in string]: EaseFn } = {
  linear: (t) => t,
  inQuad: (t) => t * t,
  outQuad: (t) => 1 - (1 - t) * (1 - t),
  inCubic: (t) => t * t * t,
  outCubic: (t) => 1 - (1 - t) * (1 - t) * (1 - t),
  inQuart: (t) => t * t * t * t,
  outQuart: (t) => 1 - (1 - t) * (1 - t) * (1 - t) * (1 - t),
  inQuint: (t) => t * t * t * t * t,
  outQuint: (t) => 1 - (1 - t) * (1 - t) * (1 - t) * (1 - t) * (1 - t),
};
// åå¼easeFnå½æ°åéçæä¸æ®µè½¬æeasingå½æ°ï¼aä¸ºå¼å§åéå¼ï¼bä¸ºç»æåéå¼
export function createEase(a: number = 0, b: number = 1, easeFn: EaseFn = baseEase.linear): EaseFn {
  const k = 1 / (b - a);
  if (!k || k <= 0) {
    return () => 0;
  }
  // easeFnåå¹³ç§»( -a , -easeFn(a) )ï¼åç¼©æ¾ k å
  // æ¥çä½¿å½æ°å¼ä¹å¨(0,1)ä¹é´ï¼å³ï¼ç»ä¸è¿°åæ¢ä¹åçå½æ°ï¼å¦åéä¸º1ï¼æ±å½æ°å¼ï¼å½æ°åé¤ä»¥è¯¥å½æ°å¼å¾å°æç»çå½æ°
  return (t: number) =>
    ((easeFn(t / k + a) - easeFn(a)) * k) / ((easeFn(1 / k + a) - easeFn(a)) * k);
}
// æä¸¤ä¸ªeasingå½æ°è¿æ¥è½¬ææ°çeasingå½æ°ï¼kä¸ºæ¯ä¾
export function createEaseInOut(
  k: number = 1 / 2,
  easeIn: EaseFn = baseEase.linear,
  easeOut: EaseFn = baseEase.linear,
): EaseFn {
  if (k <= 0) {
    return easeOut;
  }
  if (k >= 1) {
    return easeIn;
  }
  return (t: number) =>
    t <= k
      ? // easeInç´æ¥ç¼©æ¾k
        easeIn(t / k) * k
      : // easeOutè¡æ¥easeInï¼éè¦åå¹³ç§»( k/(1-k) , k/(1-k) )ï¼åç¼©æ¾ 1-k
        (easeOut(t / (1 - k) - k / (1 - k)) + k / (1 - k)) * (1 - k);
}
const easeInOutQuad = createEaseInOut(1 / 2, baseEase.outQuad, baseEase.inQuad);
const easeOutInQuad = createEaseInOut(1 / 2, baseEase.inQuad, baseEase.outQuad);
const easeOutQuad = baseEase.outQuad;
const easeOutQuart = baseEase.outQuart;

export { easeInOutQuad, easeOutInQuad, easeOutQuad, easeOutQuart };
