import type { IGestureEvent } from '../modules/gesture';
import { Value } from '../modules/transition';
import { between, isBetween } from '../utils';
import type { SwiperModel, ItemModel, ICallback } from '../core';

export default function pointerStart(
  this: SwiperModel<ItemModel | null>,
  event: IGestureEvent,
  { preventAllTap }: ICallback,
) {
  if (this._fgBehavior === 0 && event.pointers.length > 1) {
    // ç¬¬ä¸æ ¹æææ¾ä¸å»ï¼ç´§æ¥çåæ¾ä¸æ ¹ææï¼æèç´æ¥ä¸ä¸å­æ¾äºä¸¤ä¸ªææï¼ï¼æ­¤æ¶æ è®°ä¸º2
    this._fgBehavior = 2;
  }
  let stopCount = 0;
  stopCount += this.cancel();
  const item = this.currentItem();
  stopCount += item?.cancel() || 0;
  if (stopCount > 0) {
    // å¦ææå¨ç»åæ­¢ï¼æ­¤æ¶å¤äºåå¤é¨åæ¶ï¼åéè¾¹ç¼å¾ççåé¨dampingéè¦ç«é©¬æ¢å¤ï¼å¾®ä¿¡è¿ä¹å¹²çï¼
    if (item) {
      const { x: tx = 0, y: ty = 0, ...rest } = item.value().transform;
      const [xRange, yRange] = item.getTranslation();
      // æ¯å¦æ¯è¾¹ç¼å¾ç
      const isFirst = this.activeIndex() === 0;
      const isLast = this.activeIndex() === this.countItems() - 1;
      if (this.isVertical()) {
        if (
          !isBetween(tx, xRange) &&
          ((ty <= yRange[0] && !isLast) || (ty >= yRange[1] && !isFirst))
        ) {
          // éè¾¹ç¼å¾çï¼ä¸yæ¹åè¶åºè¾¹çï¼xæ¹åä¹è¶åºè¾¹ççï¼xç»äºæ¢å¤å°è¾¹ç
          item.apply(new Value({ x: between(tx, xRange), y: ty, ...rest }));
        }
      } else {
        if (
          !isBetween(ty, yRange) &&
          ((tx <= xRange[0] && !isLast) || (tx >= xRange[1] && !isFirst))
        ) {
          // éè¾¹ç¼å¾çï¼ä¸xæ¹åè¶åºè¾¹çï¼yæ¹åä¹è¶åºè¾¹ççï¼yç»äºæ¢å¤å°è¾¹ç
          item.apply(new Value({ x: tx, y: between(ty, yRange), ...rest }));
        }
      }
    }
    // å¦ææå¨ç»åæ­¢ï¼åé»æ­¢ææåæç¹å»ç¸å³äºä»¶ï¼å°±åæ¯ç§»å¨äºä¸ä¸ä¸æ ·ï¼
    preventAllTap?.();
  }
}
