import { requestAnimationFrame, cancelAnimationFrame } from './raf';

const delayCall = (callback: IAnimationCallBack, delay: number) => {
  if (delay > 0) {
    return +setTimeout(callback, delay);
  }
  callback(Math.abs(delay));
  return 0;
};

class Animation {
  duration: number; // å¨ç»æ§è¡æç»­æ¶é´
  easing: IAnimationEasing; // å¨ç»æ§è¡æ¶åæ¢å½æ°
  delay: number; // å¨ç»å»¶è¿å¤ä¹æ§è¡
  _delay: number = 0; // å»¶è¿æ§è¡å©ä½delayæ¶é´
  _frameId: number = 0; // å½åæ­£å¨æ§è¡å¸§çid
  _elapsed: number = 0; // æ¬æ¬¡å¨ç»å·²ç»æ¶èçæ¶é´
  _sleepId: number = 0; // å½åæ­£å¨ä¼ç è®¡æ¶id
  _delayId: number = 0; // å½ådelayè®¡æ¶id
  _tick: IAnimationCallBack | null = null; // æ¯ä¸å¸§å¨ç»æ§è¡å½æ°
  _delayFn: IAnimationCallBack | null = null; // å»¶è¿æ§è¡éè¦æ§è¡çå½æ°
  constructor({ duration, easing, delay }: IAnimationOptions = {}) {
    this.duration = typeof duration !== 'number' || duration <= 0 ? 0 : duration;
    this.easing = typeof easing !== 'function' ? (v) => v : easing;
    this.delay = typeof delay !== 'number' ? 0 : delay;
  }
  start(frameFn: IAnimationCallBack) {
    // åªæä»æªå¼å§è¿æçå·²ç»ç»æäºçå¨ç»å¯ä»¥åå¼å§
    if (!this._tick && !this._delayFn) {
      const { duration, easing, delay } = this;
      this._delayFn = (elapsed) => {
        this._delayId = 0;
        this._delayFn = null;
        this._delay = 0;
        if (duration > 0) {
          const tick: IAnimationCallBack = (last) => {
            // æ¬å¸§æ¶é´æ³ï¼lastï¼ä¸ä¸å¸§æ¶é´æ³
            const now = Date.now();
            // ç´¯è®¡å·²ç»èç¨çæ¶é´
            this._elapsed += now - last;
            if (this._elapsed < duration) {
              // å¼å¯ä¸ä¸å¸§
              this._frameId = requestAnimationFrame(() => tick(now));
              // æ¯ä¸å¸§çè¿åº¦
              frameFn(easing(this._elapsed / duration));
            } else {
              // æ§è¡å®æ¯åæ¸é¤
              this._tick = null;
              this._frameId = 0;
              this._elapsed = 0;
              frameFn(1);
            }
          };
          this._tick = tick;
          // ç¬¬ä¸æ¬¡ç´æ¥ç¨æè¿äºæ¶é´
          this._elapsed = elapsed || 0;
          tick(Date.now());
        } else {
          // durationä¸º0ï¼ç´æ¥è¿è¡å°æå
          frameFn(1);
        }
      };
      // è®°å½å»¶è¿å¼å§æ¶é´æ³
      this._delay = Date.now();
      // å»¶è¿æ§è¡
      this._delayId = delayCall(this._delayFn, delay);
    }
  }
  restart() {
    if (this._tick || this._delayFn) {
      // åªæå¨ç»å¨å»¶è¿ãå¨ç»ãä¼ç ï¼åæ­¢çæ¶åå¯ä»¥restart
      // éå¯ä¹åååæ­¢ä¸å
      this.stop();
      if (this._tick) {
        // å¨ç»ãä¼ç ï¼åæ­¢ç¶æï¼åæ­¢åï¼å¯å¨å¸§ç»§ç»­è¿è¡
        this._tick(Date.now());
      }
      if (this._delayFn) {
        const delay = this.delay + this._delay;
        // æ´æ°æ¶é´æ³
        this._delay += Date.now();
        // å»¶è¿ç¶æï¼åæ­¢åï¼å¼å¯æ°çå»¶è¿
        this._delayId = delayCall(this._delayFn, delay);
      }
    }
  }
  sleep(time: number) {
    if (!time || time <= 0) {
      // æ²¡æè®¾ç½®ä¼ç æ¶é´ï¼åä¸åä»»ä½æä½
      return;
    }
    if (this._tick || this._delayFn) {
      // åªæå¨ç»å¨å»¶è¿ãå¨ç»ãä¼ç ï¼åæ­¢çæ¶åå¯ä»¥è¿è¡ä¼ç 
      // è¿éæ³¨æï¼åæ­¢ç¶æçæ¶åç¹ä¼ç ï¼ç¸å½äºå»¶è¿restartï¼æ¶é´ä¸å°ä¼éå¯å¨ç»
      // sleepä¹åååæ­¢ä¸å
      this.stop();
      // å¯å¨æ¬æ¬¡ä¼ç 
      this._sleepId = +setTimeout(() => {
        this._sleepId = 0;
        // å°æ¶é´åéå¯å¨ç»
        this.restart();
      }, time);
    }
  }
  stop() {
    if (this._tick || this._delayFn) {
      if (this._delayId) {
        // å¦ææ­£å¨delayï¼ååæ­¢delay
        clearTimeout(this._delayId);
        this._delayId = 0;
        // åæ­¢å»¶è¿æ¶ï¼å·²èç¨æ¶é´
        this._delay -= Date.now();
      }
      if (this._frameId) {
        // å¦ææ­£å¨è¿è¡ï¼å³æ­£å¨å¨ç»ï¼ååæ­¢å¨ç»
        cancelAnimationFrame(this._frameId);
        this._frameId = 0;
      }
      if (this._sleepId) {
        // å¦ææ­£å¨sleepï¼ååæ­¢sleep
        clearTimeout(this._sleepId);
        this._sleepId = 0;
      }
    }
  }
  end() {
    if (this._tick || this._delayFn) {
      // åªæå¨ç»å¨å»¶è¿ãå¨ç»ãä¼ç ï¼åæ­¢çæ¶åå¯ä»¥ç´æ¥end
      // endä¹åååæ­¢ä¸å
      this.stop();
      if (this._tick) {
        // å¨ç»ãä¼ç ï¼åæ­¢ç¶æï¼åæ­¢åï¼å¯å¨å¸§è¿è¡å°æå
        this._tick(Date.now() + this._elapsed - this.duration);
      }
      if (this._delayFn) {
        // å»¶è¿ç¶æï¼åæ­¢åï¼ç´æ¥è¿è¡å°æå
        this._delayFn(this.duration);
      }
    }
  }
}

type IAnimationCallBack = (v: number) => void;

export type IAnimationEasing = (v: number) => number;

export type IAnimationOptions = {
  duration?: number; // å¨ç»æç»­æ¶é´
  easing?: IAnimationEasing; // å¨ç»åæ¢å½æ°
  delay?: number; // å¨ç»å»¶è¿å¤ä¹æ§è¡ï¼è´æ°çè¯ï¼ä¼å¨ç¬¬ä¸æ¬¡æè¿ä¸ªæ¶é´åçååä¸æ¬¡æ§ç¨æï¼å·ä½çcss3-transition-delay
};

export default Animation;
