import type { IGestureEvent } from '../modules/gesture';
import { Value } from '../modules/transition';
import { revokeDamping, performDamping } from '../modules/damping';
import { between } from '../utils';
import type { SwiperModel, ItemModel, ICallback, IOpenStyle, IBBox } from '../core';

const minScale = 0.3; // swipeCloseä¸ææå°ç¼©æ¾æ¯ä¾
const minOpacity = 0.01; // swipeCloseä¸ææå°éæåº¦
const isRightDown = (isVertical: boolean, [x0, y0]: number[], [x1, y1]: number[]) => {
  if (isVertical) {
    return Math.abs(x0 - x1) > 4 * Math.abs(y0 - y1) && x0 - x1 <= 0;
  } else {
    return Math.abs(y0 - y1) > 4 * Math.abs(x0 - x1) && y0 - y1 <= 0;
  }
};
export default function pointerMove(
  this: SwiperModel<ItemModel | null>,
  event: IGestureEvent,
  { openStyleChange }: ICallback,
) {
  if (this._fgBehavior === 0 && event.pointers.length === 1) {
    // ç¬¬ä¸æ ¹æææ¾ä¸å»ï¼ç¶åç´æ¥ç§»å¨ï¼æ­¤æ¶æ è®°ä¸º1
    this._fgBehavior = 1;
  }
  const item = this.currentItem();
  if (this.running() || item?.running()) {
    return;
  }
  const onePointer = event.pointers.length === 1 || this._fgBehavior === 1;
  const maxOption = { max: this.itemSize() };
  const {
    getPoint,
    direction,
    angle = 0,
    scale = 1,
    deltaX = 0,
    deltaY = 0,
    moveX = 0,
    moveY = 0,
  } = event;
  const point = getPoint();
  if (this._moveTarget === 'none') {
    const point0 = getPoint('previous');
    // å¤ææèä¸æ»¡è¶³ä»¥ä¸æ¡ä»¶çï¼ååå¾çæä½
    if (item) {
      this._moveTarget = 'inside';
      if (onePointer) {
        // æ¯å¦æ¯è¾¹ç¼å¾ç
        const isFirst = this.activeIndex() === 0;
        const isLast = this.activeIndex() === this.countItems() - 1;
        const rightDown = isRightDown(this.isVertical(), point0, point);
        const { x: tx = 0, y: ty = 0 } = item.value().transform;
        const [xRange, yRange] = item.getTranslation();
        // åæè¡ä¸ºæ¶ï¼æ ¹æ®å¾çä½ç½®ï¼å¤æ­åç»­ä¸ºå¤é¨swiperæä½è¿æ¯åé¨å¾çæä½
        if (this.isVertical()) {
          if (this.swipeClose() && tx >= xRange[1] && rightDown) {
            this._moveTarget = 'closures';
          } else {
            // å¦æxæ¹åæ²¡æå¯ä»¥ç§»å¨çèå´ï¼åå¤æ­åä¸è¿æ¯åä¸ä½¿ç¨deltaYçæ­£è´ï¼å¦åä½¿ç¨directionå¼
            const fixedX = xRange[0] === 0 && xRange[1] === 0;
            // å¦æxçèå´æ¯[0,0]ï¼åå¤æ­ä¸ä¸ç§»å¨ï¼ç´æ¥æ ¹æ®deltaYçæ­£è´
            // å¦åéè¦æ ¹æ®ç§»å¨æ¹åï¼å ä¸ºç§»å¨æ¹åæ¯æç§45åº¦åå¼çï¼
            const upMove = fixedX ? deltaY < 0 : direction === 'Up';
            const downMove = fixedX ? deltaY > 0 : direction === 'Down';
            // å¦æå¾çä¸è¾¹æµè¾¾æè¶åºä¸è¾¹çï¼ä»ç¶åä¸æ»å¨ï¼ä¸æ¯ç¬¬ä¸å¼ å¾ï¼
            // æèä¸è¾¹æµè¾¾æè¶åºä¸è¾¹çï¼ä»ç¶åä¸æ»å¨ï¼ä¸æ¯æåä¸å¼ å¾ï¼ï¼åä¸ºswiperï¼åä¹ä¸ºå¾çæä½
            // å ä¸ºç¬¬ä¸å¼ å¾çåæåä¸å¼ é½æ¯è¾¹ç¼å¾çï¼è¾¹ç¼å¾çåä¸¤è¾¹swipeæ¶ï¼æ¯æ²¡æä¸ä¸å¼ æä¸ä¸å¼ çï¼ï¼æ éå¤é¨swipeæä½ï¼ç´æ¥èµ°åé¨å¾çæä½
            if (
              (ty <= yRange[0] && upMove && !isLast) ||
              (ty >= yRange[1] && downMove && !isFirst)
            ) {
              this._moveTarget = 'outside';
            }
          }
        } else {
          if (this.swipeClose() && ty >= yRange[1] && rightDown) {
            this._moveTarget = 'closures';
          } else {
            // å¦æyæ¹åæ²¡æå¯ä»¥ç§»å¨çèå´ï¼åå¤æ­åå·¦è¿æ¯åå³ä½¿ç¨deltaXçæ­£è´ï¼å¦åä½¿ç¨directionå¼
            const fixedY = yRange[0] === 0 && yRange[1] === 0;
            // å¦æyçèå´æ¯[0,0]ï¼åå¤æ­å·¦å³ç§»å¨ï¼ç´æ¥æ ¹æ®deltaXçæ­£è´
            // å¦åéè¦æ ¹æ®ç§»å¨æ¹åï¼å ä¸ºç§»å¨æ¹åæ¯æç§45åº¦åå¼çï¼
            const leftMove = fixedY ? deltaX < 0 : direction === 'Left';
            const rightMove = fixedY ? deltaX > 0 : direction === 'Right';
            // å¦æå¾çå·¦è¾¹æµè¾¾æè¶åºå·¦è¾¹çï¼ä»ç¶åå³æ»å¨ï¼ä¸æ¯ç¬¬ä¸å¼ å¾ï¼
            // æèå³è¾¹æµè¾¾æè¶åºå³è¾¹çï¼ä»ç¶åå·¦æ»å¨ï¼ä¸æ¯æåä¸å¼ å¾ï¼ï¼åä¸ºswiperæä½ï¼åä¹ä¸ºå¾çæä½
            if (
              (tx <= xRange[0] && leftMove && !isLast) ||
              (tx >= xRange[1] && rightMove && !isFirst)
            ) {
              this._moveTarget = 'outside';
            }
          }
        }
      }
    } else {
      this._moveTarget =
        this.swipeClose() && isRightDown(this.isVertical(), point0, point) ? 'closures' : 'outside';
    }
  }
  // è¿å¥åé¨å¾çæä½
  if (this._moveTarget === 'inside') {
    if (item) {
      // è®¡ç®åºå¤é¨swiperç§»å¨çåå§è·ç¦»diff
      const translate = -this.activeIndex() * this.itemSize();
      const diff = revokeDamping(this.value().transform.default - translate, maxOption);
      let _deltaX = deltaX;
      let _deltaY = deltaY;
      // æswiperç§»å¨çåå§è·ç¦»å è¿å»
      if (this.isVertical()) {
        _deltaY += diff;
      } else {
        _deltaX += diff;
      }
      if (onePointer) {
        const { x: tx = 0, y: ty = 0 } = item.value().transform;
        const [xRange, yRange] = item.getTranslation();
        let _delta = 0;
        if (this.isVertical()) {
          // å¦æxæ¹åæ²¡æå¯ä»¥ç§»å¨çèå´ï¼åå¤æ­åä¸è¿æ¯åä¸ä½¿ç¨deltaYçæ­£è´ï¼å¦åä½¿ç¨directionå¼
          const fixedX = xRange[0] === 0 && xRange[1] === 0;
          _delta = _deltaY;
          // ç«åçæ¶ååºå®x
          _deltaX = fixedX ? 0 : _deltaX;
          // åé¨å¾çï¼æå¤ç§»å¨å°è¾¹ç
          _deltaY = between(_deltaY + ty, yRange) - ty;
          // è¶åºé¨åç±å¤é¨swiperç§»å¨
          _delta -= _deltaY;
        } else {
          // å¦æyæ¹åæ²¡æå¯ä»¥ç§»å¨çèå´ï¼åå¤æ­åå·¦è¿æ¯åå³ä½¿ç¨deltaXçæ­£è´ï¼å¦åä½¿ç¨directionå¼
          const fixedY = yRange[0] === 0 && yRange[1] === 0;
          _delta = _deltaX;
          // åé¨å¾çï¼æå¤ç§»å¨å°è¾¹ç
          _deltaX = between(_deltaX + tx, xRange) - tx;
          // æ¨ªåçæ¶ååºå®y
          _deltaY = fixedY ? 0 : _deltaY;
          // è¶åºé¨åç±å¤é¨swiperç§»å¨
          _delta -= _deltaX;
        }
        // åé¨å¾çéè¦ç§»å¨çè·ç¦»
        item.moveBounce(0, 1, _deltaX, _deltaY, point);
        // å¤é¨swiperéè¦ç§»å¨çè·ç¦»
        this.apply(new Value(translate + performDamping(_delta, maxOption)));
      } else {
        item.moveBounce(angle, scale, _deltaX, _deltaY, point);
        if (diff !== 0) {
          // å¤æç§»å¨åï¼å°å¤é¨swiperç§»å¨çé¨åå½ä½ï¼åé¨imageä¼æswiperçè·ç¦»å è¿å»ï¼
          this.apply(new Value(translate));
        }
      }
    }
    return;
  }
  if (this._moveTarget === 'closures') {
    openStyleChange?.((prevStyle: IOpenStyle, viewBox: IBBox) => {
      const { width = 0, height = 0, left = 0, top = 0 } = viewBox;
      const k = Math.min(
        Math.max(1 - ((this.isVertical() ? moveX / width : moveY / height) || 0), minScale),
        1,
      );
      const o = minOpacity + ((k - minScale) * (1 - minOpacity)) / (1 - minScale);
      const { k: _k = 1, x: _x = 0, y: _y = 0 } = prevStyle;
      const x = _x + deltaX + (point[0] - (_x + left + width / 2)) * (1 - k / _k);
      const y = _y + deltaY + (point[1] - (_y + top + height / 2)) * (1 - k / _k);
      return { o, k, x, y };
    });
    return;
  }
  // éåé¨å¾çæä½ï¼åæ¯å¤é¨swiperæä½
  const swiperRange = [(1 - this.countItems()) * this.itemSize(), 0];
  // åæå½åå¼åç®åºé»å°¼ä¹åçåå¼
  let bt = between(this.value().transform.default, swiperRange);
  let t = bt + revokeDamping(this.value().transform.default - bt, maxOption);
  // åå¯¹æ»å¼è¿è¡æ»ä½é»å°¼è®¡ç®
  bt = between((t += this.isVertical() ? deltaY : deltaX), swiperRange);
  t = bt + performDamping(t - bt, maxOption);
  this.apply(new Value(t));
}
